





















































































































































































import Component from 'vue-class-component';
import VueBase from '@/utils/VueBase';
import { dataManager } from '@/utils/DataManager';
import { MVList as ItemMVList } from '@/master/item';
import { ItemModifier } from '@/logic/modifiers/ItemModifier';
import { clamp } from 'lodash';

@Component({
  components: {
  },
})
export default class extends VueBase {
  public get dataManager() {
    return dataManager;
  }

  public item: ItemMVList | null = null;

  public itemModifier = new ItemModifier();

  public beforeMount() {
    this.item = dataManager.itemById[this.$route.query.df as string];
    if (!this.item) {
      this.$router.push({ name: 'Items' });
    }

    const maxQuality = this.item.EQU_BRD ? 120 : 100;
    this.itemModifier.quality = this.$route.query.quality ? clamp(+this.$route.query.quality, 0, maxQuality) : maxQuality;
    this.itemModifier.level = 80;
  }
}
